.counter-up{
    margin-bottom: 150px;
    background: url(/static/media/backgroundimgservices.43acc691.jpg)no-repeat;
    min-height: 80vh;
    display: flex;
    align-items: center;
    padding: 0 50px;
    background-size: cover;
    background-attachment: fixed;
    position: relative;
}

.counter-up::before{
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: rgba(0,0,0,0.8);
    
}
.counter-up .content{
    position: relative;
    z-index: 2;
    display: flex;
    width: 100%;
    height: 100%;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
}
.content .box{
    width: calc(25% - 35px);
    border: 1px dashed rgba(255,255,255,0.6);
    border-radius: 5px;
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    color: #fff;
    border-radius: 5px;
    flex-direction: column;
    margin: 15px;
}

