    font-size: 50px;
   color: #f2f2f2;
   font-weight: 500;
   font-family:sans-serif;

   }

   .content .box .text{
       color: #ccc;
       
   }


   @media (max-width: 1036px){
       .counter-up{
           padding: 50px 50px 0 50px;
       }
